@import url('https://fonts.googleapis.com/css2?family=Antic+Didone&family=Raleway:ital,wght@1,600&display=swap');

html{
  overflow: hidden;
}
* h1,h3, p{
  margin: 0;
}

body{
  position: relative;
  z-index: 1;
  overflow: hidden;
  max-width: 100vw;
  max-height: 100vh;
  width: 100%;
  min-height: 100%;  
  font-size: 10px;
}

.App {
  display: flex;
  justify-content: center;
  align-items: center;


}
.task{
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem;
  background-color: cornflowerblue;
}

.todoList-app {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  min-height: 100vh;
  padding: 0 2rem;
  background-image: url(./assets/bg-image.jpg);
  background-position-y: 30%;
  background-position-x:0;
  background-size: cover;
  overflow: hidden;
  scroll-behavior: none;
  position: relative;
  z-index: 10;
}


.header {
  text-align:center;
  font-family: 'Antic Didone', serif;
  font-size: 2.5rem;
  color: #00008d;
  text-transform: uppercase;
  padding: 1.45rem 0;
}


.main-container{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  z-index: 1;
  position: relative;
}

.btn-add-modal{
  max-width: 400px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  border: none;
  background: none;
  z-index: 10;
  font-family: 'Antic Didone', serif;
  color: #00008d;
  font-size: 1.5rem;
}
.btn-add-modal h3{
  margin:0 ;
}

.tasks-container{
  width: 100%;
  max-width: 510px;
  position: relative;
  z-index: 30;
  padding: 3.25rem 0 0;
  margin: 0 auto;
}
.task-container{
  display: flex;
  align-items: center;
  justify-content: space-between;   
  padding: .25rem 0 ;
  max-width: 310px;
  border-bottom: 0.5px solid rgba(179, 172, 172, 0.755);
}
.task-container:last-child{
  border-bottom: none;
}
.text-container{
  display: flex;
  color: #00008d;
  font-family: 'Raleway', sans-serif;
  font-size: 1.3rem;
}
.text-container p:last-child{
  margin: 0;
  padding: 0;
}

.btn-container{
  display: flex;
}

.btn{
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  background: none;
  max-width: 30px;
}
.btn img{
  width: 100%;
  object-fit: contain;
  min-height: 20px;
}

.counter-task{
  padding: 0 .35rem 0 0;
  margin: 0;
}











/*  Modal  */
.modal-overlay{
  width: 100vw;
  height: 100vh;
  position: fixed;
  box-shadow: 0 3px 38px 0 rgba(0, 0, 0, 0.1);
  background-color: rgba(0, 0, 0, 0.5);
  display: none;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  overflow:hidden;
  opacity: 1;
  z-index: 100;
  padding: 5% 0;

}
.modal {
  position: absolute;
  top: 50%;
  width: 50%;
  max-width: 450px;
  min-height: 200px;
  padding: 1rem;
  left: 50%;
  transform: translate(-50%,-50%);
  background-color: white;
  border: 1px solid grey;
  border-radius: 3px;
}
.modal h3{
  text-transform: capitalize;
  font-family: 'Antic Didone', serif;
  color: #00008d;
  font-size: 2rem;
}
.modal p{
  text-transform: capitalize;
  font-family: 'Raleway', sans-serif;
  font-size: 1rem;
  padding: 1.5rem 0 ;
}
.modal form{ 
  display: flex;
  align-items: center;
  text-transform: capitalize;
  padding: 0 0 2rem;
}
.btn-add-modal img{
  width: 100%;
  max-width: 35px;
  object-fit: contain;
}
.close-btn{
  font-size: 1rem;
  line-height: 1.5;
  border-radius: .25rem;
  text-align: center;
  white-space: nowrap;
  color: #dc3545;
  background-color: transparent;
  border-color: #dc3545;
  padding: .375rem .75rem;
  transition: color .1s ease-in-out, background-color .15s ease-in-out, border-color .15s ease-in-out, box-shadow .15s ease-in-out;
}
button.close-btn:hover{
  color: #ffffff;
  background-color:  #dc3545;

}
form input{
  border-radius: 10px;
  outline: none;
  min-height: 30px;
  font-size: 16px;
  padding: 0 .375rem;
  max-width: 80%;
}

@media (max-width: 520px) {
  .todoList-app{
    background-position-x: 45%;
  }
  .header{
    letter-spacing: 0.09rem;
    font-size: 2.8em;
    line-height: 2.2rem;
    padding: .5rem  2rem  1.5rem 0;

  }
  .main-container{
    flex-direction: column;
    align-items: flex-start;
  }
  .tasks-container{    
    padding: 6.75rem 0 0;
    max-width:290px;
    margin: 0 auto;
  }
  .task-container{
    max-width: 200px;
  }
  .text-container {
    font-size: .85rem;
    line-height: .8rem;
  }
 
  .btn-add-modal{
    flex-direction: row;
    justify-content: space-evenly;
    position: relative;
    font-size: 1rem;
    margin: 0 auto;
    padding: 0 2.25rem;
  }
  .btn-add-modal img{
    max-width: 25px;
  }
  .modal{
    width: 70%;
  }
  .modal h3{  
    font-size: 1.5rem;
  }
  .modal p{  
    font-size: 0.9rem;
  }
  
}
@media (min-width:521px) and (max-width:767px) {
  .header{
    letter-spacing: 0.15rem;
    font-size: 3em;
    line-height: 2.6rem;
  }
  .todoList-app{
    background-position-x: 61%;
  }
  .tasks-container{    
    max-width: 465px;
    margin: 0 auto;
    padding: 4.25rem 0 0;

  }
  .btn-add-modal{
    font-size: 1.2rem;
  }
  .task-container{
    max-width: 210px;
  }
  .text-container {
    font-size: .9rem;
    line-height: .8rem;
  }

  .modal h3{  
    font-size: 1.5rem;
  }
}
@media (min-width: 768px) and (max-width: 1023px) {
  .todoList-app{
    background-position-x: 70%;
  }
  .header{
    font-size: 2.3rem;
    line-height: 2.5rem;
    padding: 1rem  2rem 2.75rem 0
  }
  .main-container{
    flex-direction: column;
    align-items: center;
  }
  .tasks-container{    
    max-width: 460px;
    margin: 0 auto;
    padding: 3.25rem 0 0 .25rem;
  }
  .task-container{
    max-width: 205px;
  }
 
  .text-container {
    font-size: .95rem;
    line-height: 1.2rem;
  }
  
  .btn-add-modal{
    flex-direction: row;
    justify-content: space-between;
    position: relative;
    max-width: 350px;
    font-size: 1.3rem;
  }
  .btn-add-modal img{
    max-width: 25px;
  }
 
}
@media (min-width: 1024px) and (max-width: 1311px) {
  .todoList-app{
    background-position-x: 75%;
  }
  .header{
    font-size: 3.2em;
    line-height: 3rem;
    padding: 1rem 2rem 2rem 0;

  }
  .main-container{
    flex-direction: column;
    align-items: center;
  }
  .tasks-container{   
    max-width: 420px;
    margin: 0 auto;
  }
  .task-container{
    max-width: 230px;
  }
  .text-container{
    font-size: 1rem;
    line-height: 1.4rem;
    max-width: 200px;
  }
  
  .btn-add-modal{
    flex-direction: row;
    justify-content: space-between;
    position: relative;
    max-width: 385px;
    font-size: 1.4rem;
  }
  .btn-add-modal img{
    max-width: 25px;
  }
  
  
  .text-container {
    font-size: 1.1rem;
    line-height: 1.4rem;
  }
  .modal{
    width: 40%;
  }
}
@media (min-width: 2000px) {
  .todoList-app{
    background-position-y: 45%;
  }
  .header{
    font-size: 2.6em;
    line-height: 3.2rem;
    padding: 0  00 0;
    margin: 0;

  }
  .main-container{
    flex-direction: column;
    align-items: center;
  }
  .tasks-container{      
    max-width: 600px;   
    padding: .25rem 0 .25rem 1.25em;



  }
  .task-container{
    max-width: 365px;
  }
  .text-container {
    font-size: 1.3rem;
    line-height: 1.8rem;
}

  
  .btn-add-modal{
    flex-direction: row;
    justify-content: space-between;
    position: relative;
    max-width: 400px;
    width: 100%;
    padding: 0 0 1em;
  }
  .btn-add-modal img{
    max-width: 50px;
  }
  
  .modal{
    padding: 1rem;
    width: 40%;
    max-width: 850px;
    min-height: 300px;
  }
  .modal form{
    padding: 2rem 0;
  }
  
  .modal  h3{
    font-size: 2rem;
    line-height: 2.2rem;
  }
  .modal form input{
    border-radius: 10px;
    outline: none;
    min-height: 40px;
    font-size: 18px;
    padding: 0 .775rem;
  }
  .btn {
    max-width: 45px;
  }
  .btn img{
    min-height: 35px;
    max-width: 35px;
  }
  .close-btn{
    font-size: 1.4rem;
    line-height: 1.5;
    border-radius: .25rem;
    padding: .775rem .75rem;
    transition: color .1s ease-in-out, background-color .15s ease-in-out, border-color .15s ease-in-out, box-shadow .15s ease-in-out;
  }
}



